.mark-name {
    margin: 10px 4px;
    background: #c5c4c4;
    padding: 3px;
    border-radius: 4px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.mark-name button {
    cursor: pointer;
    border: none;
    background: #585858;
}

.mark-name button:active {
    border: none;
}

.mark-name button:hover {
    background: #fc6666;
}

.mark-name button:focus {
    outline: none;
}


